  #sidebar-btn {
    pointer-events: none;
  }
}
.seller-minimize {
  display: none;
}

.logo-minimize {
  display: none;
}

.minimize {
  grid-template-columns: 117px 1fr;
}
.minimize aside {
  padding: 34px 14px;
}
.minimize .sidemenu-item {
  width: 62px;
  height: 62px;
  display: grid;
  place-items: center;
  margin: 0 auto;
}
.minimize .seller-minimize,
.minimize .logo-minimize {
  display: block;
}
.minimize .sidemenu-item .sidemenu-title,
.minimize .category-list,
.minimize .seller-maximize,
.minimize .upgrade-card,
.minimize .logo-maximize,
.minimize .child-menu,
.minimize .caret-icon {
  display: none;
}
.minimize .sidemenu-item.active .child-menu {
  display: block;
  position: absolute;
  top: 0px;
  left: 122%;
  background: var(--gray-100);
  padding: 20px;
  border-radius: 8px;
  border: 1px solid var(--neutral-border);
  min-width: 150px;
  color: var(--gray-500);
}
.minimize .sidebar-control {
  max-width: 90px;
  padding: 15px 11px;
  gap: 13px;
}
.minimize #toggle-theme-btn {
  opacity: 0;
  visibility: hidden;
  display: none;
}

.dark .minimize .sun-icon {
  display: none;
}
.dark .minimize .sidemenu-item.active .child-menu {
  background: var(--gray-900);
  border-color: var(--gray-700);
}

html:not(.dark) .minimize .moon-icon {
  display: none;
}

